
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import service from "@/uilt/request";
import { orderList, recOrder,orderMsg,kclist } from "@/api/order";
export default {
components: {
Panel,
Group,
Radio,
Confirm
},
watch: {
type: function(old, news) {
this.list=[];
this.getData();
},
userid:function(old,news){

}
},
methods: {
onImgError(item, $event) {
console.log(item, $event);
},
getData() {
this.$vux.loading.show({ text: "Loading" });
var dataset = this;
debugger
if(this.type!=4)
orderList(this.pageindex, 6, this.userid, this.type)
.then(function(result) {
dataset.$vux.loading.hide();
if (result.data.length > 0) {
dataset.pageindex++;
dataset.title = "当前拥有 " + result.count + " 条订单数据";
for (var i = 0; i < result.data.length; i++) {
var order = result.data[i];
dataset.list.push(dataset.CrateItem(order));
}
} else {
dataset.$vux.toast.show({ text: "没有更多数据了" });
}
})
.catch(function(result) {
console.log(result);
dataset.$vux.loading.hide();
alert("获取超时");
});
else  kclist(this.pageindex, 6, this.points.lat, this.points.lon)
.then(function(result) {
debugger
dataset.$vux.loading.hide();
if (result.data.length > 0) {
dataset.pageindex++;
dataset.title = "当前拥有 " + result.count + " 条订单数据";
for (var i = 0; i < result.data.length; i++) {
var order = result.data[i];
dataset.list.push(dataset.CrateItem(order));
}
} else {
dataset.$vux.toast.show({ text: "没有更多数据了" });
}
})
.catch(function(result) {
console.log(result);
dataset.$vux.loading.hide();
alert("获取超时");
});
},
CrateItem(order) {
var t = {
desc: "",
meta: { source: "来源信息", date: "时间", other: "" },
data: null
};
t.desc =
"从" + order.startlocation + "到" + order.endlocation + "的订单.";
t.meta.source = "价格:" + order.driverprice;
t.meta.date = order.startTime.split("+")[0].replace("T", " ");
t.data = order;
return t;
},
onConfirm(){
this.recOrderFun(this.tmp);
},
commit(item) {
console.log(item);
this.show=true;
this.tmp=item;
},
recOrderFun(item){
console.log(this.driverInfo);
var obj = {
state: 4,
driverid: this.driverInfo.userid,
driverInfo: JSON.stringify(this.driverInfo)
};
var dataset=this;
var userinfo= JSON.parse( item.data.ordersInfo);
recOrder(item.data.id, obj).then(function(data) {
if (data) {
var msg =
"您从" +userinfo.startingPoint +"到" +
userinfo.endingPoint +"的订单已经被接收。\r\n司机称呼：" +
dataset.driverInfo.usercall + "\r\n联系方式:" +
dataset.driverInfo.mobile;

var msg2 =
"您接收从" +userinfo.startingPoint +
"到" +userinfo.endingPoint +
"的订单已经被接收。\r\n用户称呼：" +userinfo.nickname +
"\r\n联系方式:" +
userinfo.rider +"\r\n时间：" +
userinfo.StartTime;

dataset.$vux.toast.show({ text: '接单成功。' });
orderMsg(userinfo.openid,msg);
console.log("driverInfo",dataset.driverInfo);
orderMsg(dataset.driverInfo.openid,msg2);
} else dataset.$vux.toast.show({ text: "订单已经被其他用户接单。" });
});
},
onfooter() {
this.getData();
}
},
data() {
return {
list: [],
title: "",
footer: {
title: "获取更多..."
},
pageindex: 1,
show:false,
tmp:{}
};
},
created() {
this.getData();
},
props: ["type", "userid", "driverInfo","points"]
};



